<template>
    <div id="background">
    <form v-on:submit.prevent ="request">
         <b-field class="centerme">
            <b-input placeholder="Search..."
                class="searchbox"
                type="search"
                icon-pack="fas"
                icon="search"
                v-model="searchText"
               >
            </b-input>
        </b-field>
    </form>
    <span class="allignmee">
            <b-field class="positionme" label="No of Images">
            <b-select placeholder="Select" rounded  v-model="Noimages" v-on:change="changedValue()">
                <option>10</option>
                <option>20</option>
                <option>30</option>
            </b-select>
        </b-field>
            <b-field class="positionme" label="   Orientation"  >
            <b-select placeholder="Select" rounded  v-model="selection">
                <option>vertical</option>
                <option>horizontal</option>
                <option>all</option>
            </b-select>
        </b-field>
            <b-field class="positionme" label="category"  >
            <b-select placeholder="Select" rounded  v-model="category">
                <option>backgrounds</option>
                <option>fashion</option>
                <option>nature</option>
                <option>science</option>
                <option>education</option>
                <option>health</option>
                <option>people</option>
                <option>religion</option>
                <option>places</option>
                <option>animals</option>
                <option>industry</option>
                <option>computer</option>
                <option>food</option>
                <option>sports</option>
                <option>transportation</option>
                <option>travel</option>                            
            </b-select>
        </b-field>
            <b-field class="positionme" label="order"  >
            <b-select placeholder ="Select" rounded  v-model="order">
                <option>Popular</option>
                <option>Latest</option>
            </b-select>
        </b-field>         
    </span>


    <br>
    <span v-if="resultfound" class="allignme">Search Results for <span >{{this.resultname}}</span>..</span>
    <br><br>
    <div style="background:#f6f5fa">
        <section class="columns is-multiline" >
        <figure v-for="image in images" :key="image.id" class="column is-one-third">
          <img class="cursor" :src='`${image.webformatURL}`' @click="imageModal(`${image.webformatURL}`)">
      </figure>
        </section>
    </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            searchText: '',
            previousText: this.$route.params.message,
            apiUrl:'https://pixabay.com/api/',
            apiKey:'23294204-5ba3b34b4c9aaa6b3bef520eb',
            images:[],
            resultfound: false,
            resultname:'',
            Noimages: '20',
            selection: 'all',
            order: 'Popular',
            selectedimage: [],
            status: false,
            category: '',
            iscategory: false,
        };
    },
    async created() {
            try {
             const res = await axios.get(`${this.apiUrl}/?key=${this.apiKey}&q=${this.previousText}&image_type=photo&per_page=30&safesearch=true`)
             this.images = res.data.hits
             this.resultfound = !this.resultfound
             this.resultname = this.previousText
             this.status = true
             console.log(this.images)
            }catch(err) {
              console.log(err)
            }
     },
     methods: {
         async request() {
            try {
             const res = await axios.get(`${this.apiUrl}/?key=${this.apiKey}&q=${this.searchText}&image_type=photo&per_page=${this.Noimages}&orientation=${this.selection}&order=${this.order}safesearch=true`)
             this.images = res.data.hits
             console.log(this.images)
             this.resultname = this.searchText
             this.status = false
             this.iscategory = false;
             this.category = ''
             this.resultfound = true
            }catch(err) {
              console.log(err)
            }            
         },
         imageModal(src) {
               console.log(src)
               console.log(this.selectedimage)
                const h = this.$createElement
                const vnode = h('p', { class: "modalimage" }, [
                    h('img', { attrs: { src: src }})
                ])
                this.$buefy.modal.open({
                    content: [ vnode ]
                })
            },
            async createdbyme() {
             try {
               const res = await axios.get(`${this.apiUrl}/?key=${this.apiKey}&q=${this.previousText}&image_type=photo&per_page=${this.Noimages}&orientation=${this.selection}&order=${this.order}safesearch=true`)
               this.images = res.data.hits
               this.resultfound = true
               this.resultname = this.previousText
               console.log(this.images)
               this.iscategory = false;
            } catch(err)  {
                 console.log(err)
            }
        },
            async categoryrequest() {
             try {
               const res = await axios.get(`${this.apiUrl}/?key=${this.apiKey}&category=${this.category}&image_type=photo&per_page=${this.Noimages}&orientation=${this.selection}&order=${this.order}safesearch=true`)
               this.images = res.data.hits
               this.resultfound = true
               this.resultname = this.category
               console.log(this.images)
               this.iscategory = true;
            } catch(err)  {
                 console.log(err)
            }
        },       
            
     },
     watch : {
         selection: function() {
            if(this.iscategory == true){
                this.categoryrequest()
            } else if(this.searchText !== null){
                this.createdbyme()
                console.log("somethingiswrong")
            }else {
                this.request()
                console.log("nothingiswrong")
            }
         },
         order: function() {
           if(this.iscategory == true){
                this.categoryrequest()
            } else if(this.searchText !== null){
                this.createdbyme()
                console.log("somethingiswrong")
            }else {
                this.request()
                console.log("nothingiswrong")
            }
         },
         Noimages : function() {
            if(this.iscategory == true){
                this.categoryrequest()
            } else if(this.searchText !== null){
                this.createdbyme()
                console.log("somethingiswrong")
            }else {
                this.request()
                console.log("nothingiswrong")
            }
         },
         category : function() {
            this.categoryrequest()
         }
     }
}
</script>

<style scoped>
.cursor {
    cursor: pointer;
}
.modalimage {
    overflow: hidden;
}
@media (min-width:640px) {
    .modalimage {
        padding-left: 10vw;
    }
}
.allignmee {
    display: inline-flex;
    flex-wrap: wrap;
    margin-top: 5px;
}
.positionme {
    margin-left: 20px;
}
.padding {
    padding-left: 20px;
    padding-top: 5px;
    color: white;
}
.centerme {
    padding-top: 5vh ;
}
.searchbox {
  width: 96vw;
  margin-left: 1vw;
  margin-right: 1vw;
}
.allignme {
    padding-left: 10px;
}
.image{ 
    max-width: 50vw;
}
#background {
    background-color: rgb(221, 233, 236);
}
</style>